// =============================================================================
// Manage subscription - Account selector
// =============================================================================

.#{$wpfs}-account-selector {
  $root: &;

  &-list {
    margin-bottom: spacer(4);
  }

  &-item {
    display: flex;
    align-items: center;
    padding: spacer(2) 0;
    border-top: 1px solid var(--form-secondary-color);

    &:last-of-type {
      border-bottom: 1px solid var(--form-secondary-color);
      margin-bottom: spacer(1);
    }
  }

  &-data {

  }

  &-name {
    display: block;
    line-height: 24px;
    font-size: 15px;
    margin-bottom: spacer(0.5);
  }

  &-meta {
    color: var(--silver-color);
    line-height: 16px;
    font-size: 13px;
  }

  &-actions {
    margin-left: auto;
  }
}
